.BankAccount{
    &-account{
        display: flex;
        flex:0;
        justify-content: space-between;
        align-items: center;
        border: 1px solid black;
        background-color: #fff;
        width: 80%;
        margin: 0 auto;
        flex-direction: column;
        padding: 1.5rem;
        box-sizing: border-box;
        text-align: left;
        margin-bottom: 2rem;
        &_details{
            display: block;
            width: 200px;
            padding: 8px;
            font-size: 1.1rem;
            font-weight: bold;
            margin-top: 1rem;
            border-color: #00bc77;
            background-color: #00bc77;
            color: #fff;
        }
        &-wrapper{
            width:100%;
            flex:1;
        }
        &_title {
            margin:0;
            padding:0;
            font-size:1rem;
            font-weight:normal;
        }
        &_amount {
            margin: 0;
            font-size: 2.5rem;
            font-weight: bold;
            &-description {
                margin:0;
            }
        }
    }
}
.cta{
    flex:1;
}